import React from 'react'
import { connect } from 'react-redux'
import PropTypes from 'prop-types'
import { bindActionCreators } from 'redux'
import functional from 'react-functional'
import { fullName } from '../../utils.js'
import './Client.css'
import { setClient } from '../../actions/index.js'

// Client :: Object -> Object
const Client = props => {
    const client = props.client
    const hasClient = undefined !== client && null !== client && 0 !== Object.keys(client).length

    if(!hasClient) {
        return null
    }

    return (
        <div className="Client">
            <div className="avatar">
                <img src={ client.general.avatar } alt={ fullName(client.general) }/> 
            </div>
            <div className="about">
                { fullName(client.general) }

                <div>
                    <h3> Job </h3>
                    <p> <span> Company: </span> { client.job.company } </p>
                    <p> <span> Title: </span> { client.job.title } </p>
                </div>

                <div>
                    <h3> Contacts </h3>
                    <p> <span> Email: </span> { client.contact.email } </p>
                    <p> <span> Phone: </span> { client.contact.phone } </p>
                </div>

                <div>
                    <h3> Address </h3>
                    <p> <span> Country: </span> { client.address.country } </p> 
                    <p> <span> City: </span> { client.address.city } </p>
                    <p> <span> Street: </span> { client.address.street } </p>
                    <p> <span> Zip code: </span> { client.address.zipCode } </p>
                </div>
            </div>
        </div>
    )
}

Client.propTypes = {
    client: PropTypes.object
}

// componentDidMount :: Object -> Void
Client.componentDidMount = props => {
    document.onkeydown = e => {
        if(27 === e.keyCode) {
            dispatch(props.actions.setClient({}))
        }
    }
}

// state :: Object -> Object
const state = store => ({ client: store.client })

// dispatch :: Function -> Object
const dispatch = dispatch => ({
    actions: bindActionCreators({
        setClient
    }, dispatch)
})


export default connect(state, dispatch)(functional(Client))


