import React from 'react'
import './App.css'
import Clients from '../clients/Clients.js'
import Client from '../client/Client.js'

// App :: Object -> Object
const App = () => (
  <div className="App">
    <div className="container">
      <div className="left"> <Clients/> </div>
      <div className="right"> <Client/> </div>
    </div>
  </div>
)

export default App



