import React from 'react'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import functional from 'react-functional'
import './Search.css'
import clients from '../../data/clients.json'
import { setClients, fetchClients } from '../../actions/index.js'
import flat from 'flat'

// Search :: Object -> Object -> Array
const search = (e, props) => {
    const query = e.target.value.trim()
    
    if(0 === query.length && props.clients !== clients.length) {
        dispatch(props.actions.fetchClients())
        e.target.value = ''

        return
    }

    const result = []
    const notResolveKeys = ['general.avatar']
    const toSearch = clients.map(flat)

    toSearch.forEach((client, index) => {
        for(const key of Object.keys(client)) {
            if(!notResolveKeys.includes(key) && client[key].toString().includes(query)) {
                result.push(clients[index])

                break;
            }
        }
    })
    
    dispatch(props.actions.setClients(result))
}

// Search :: Object -> Object
const Search = props => (
    <div className="Search">
        <input type="text" placeholder="..." id="search" onChange={e => search(e, props)}/>
    </div>
)

// dispatch :: Function -> Object
const dispatch = dispatch => ({
    actions: bindActionCreators({
        fetchClients, setClients
    }, dispatch)
})


export default connect(null, dispatch)(functional(Search))


