import clients from '../data/clients.json'

// setClients :: Array -> Object
export const setClients = clients => ({
    type: 'SET_CLIENTS',
    payload: { clients }
})

// setClient :: Object -> Object
export const setClient = client => ({
    type: 'SET_CLIENT',
    payload: { client }
})

// fetchClients :: Object
export const fetchClients = () => setClients(clients)


