import React from 'react'
import { connect } from 'react-redux'
import PropTypes from 'prop-types'
import { bindActionCreators } from 'redux'
import functional from 'react-functional'
import './Clients.css'
import Search from './Search'
import { fullName } from '../../utils.js'
import { fetchClients, setClient } from '../../actions/index.js'

// showClientInfo :: Function -> Object -> Function
const showClientInfo = (action, client) => dispatch(action(client))

// clientIsActive :: Object -> Object -> String
const clientIsActive = (client, activeClient) => {
    const clientAvatar = client.general.avatar
    const activeClientAvatar = undefined === activeClient.general ? '' : activeClient.general.avatar

    return clientAvatar === activeClientAvatar ? 'client activeClient' : 'client'
}

// Clients :: Object -> Object
const Clients = props => (
    <div className="Clients">
        <Search/>
        { props.clients.map((client, i) => {
            return (
                <div className={clientIsActive(client, props.activeClient)} 
                     key={i} 
                     onClick={ e => showClientInfo(props.actions.setClient, client) }>
                    <div className="avatar">
                        <img src={ client.general.avatar } alt={ fullName(client.general) }/>
                    </div>
                    <div className="info">
                        <p className="fullname">{ fullName(client.general) }</p>
                        <p className="job">{ client.job.title }</p>
                    </div>
                </div>
            )
        }) }
    </div>
)

// componentDidMount :: Object -> Object
Clients.componentDidMount = props => props.actions.fetchClients()
Clients.propTypes = { clients: PropTypes.array }

// state :: Object -> Object
const state = store => ({ clients: store.clients, activeClient: store.client })

// dispatch :: Function -> Object
const dispatch = dispatch => ({
    actions: bindActionCreators({
        fetchClients, setClient
    }, dispatch)
})


export default connect(state, dispatch)(functional(Clients))


